<script setup>
import {onMounted, ref, watch} from "vue";
import {useRoute, useRouter} from 'vue-router';
import {mainStore} from "@/pinia";

const store = mainStore()
const router = useRouter()
const route = useRoute()
//判断当前主题
const act = ref(1)
//右上角五个选项点击（宽度>719px才显示）
const naviAct = ref(null)
//选择主题时的弹出框动画
const isShow = ref(false)
//选择主题时的弹出框动画
const donghua = ref({
  transform: 'translate(-50%, 0px)',
  opacity: 0,
  display: 'none',
})
//侧边栏显示
const drawer = ref(false)
//侧边栏弹出下>前端按钮的下拉框
const ulShow = ref(false)
const leftUl = ref({
  opacity: 0,
  display: 'none',
})

//页面路由跳转
function goUrl(e, y) {
  naviAct.value = y
  router.push(e)
}

//点击div外部,隐藏弹出框
function onClickOutside(e) {
  donghua.value = {
    transform: 'translate(-50%, 0px)',
    opacity: 0,
  }
  setTimeout(() => {
    donghua.value.display = 'none'
  }, 200)
}

//弹出选择颜色主题的mode框
function clickColor() {
  isShow.value = !isShow.value
  if (isShow.value) {
    donghua.value = {
      transform: ' translate(-50%, -10px)',
      opacity: 1
    }
  }
  if (!isShow.value) {
    donghua.value = {
      transform: `translate(-50%, 0px)`,
      opacity: 0,
      // display: 'none',
    }
    setTimeout(() => {
      donghua.value.display = 'none'
    }, 200)
  }
}

//更改mode
function colorChange(e) {
  act.value = e
  if (e === 0) {
    localStorage.setItem('mode', 'dark')
    store.background = '#181818'
  }
  if (e === 1) {
    localStorage.setItem('mode', 'auto')
    let date = new Date()
    let h = date.getHours() //小时
    if (h >= 7 && h <= 18) {
      //白天
      store.background = '#fff'
    } else {
      //黑天
      store.background = '#181818'
    }
  }
  if (e === 2) {
    localStorage.setItem('mode', 'light')
    store.background = '#fff'
  }
}

function ulclick() {
  ulShow.value = !ulShow.value
  if (ulShow.value) {
    leftUl.value.opacity = 1
    delete leftUl.value.display
  } else {
    leftUl.value.opacity = 0
    setTimeout(() => {
      leftUl.value.display = 'none'
    }, 200)
  }
}

onMounted(() => {
  if (localStorage.getItem('mode')) {
    let m = localStorage.getItem('mode')
    act.value = m === 'dark' ? 0 : m === 'auto' ? 1 : 2
  }
})
</script>

